.modal {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: 3rem 1rem;

    &__close {
        position: absolute;
        top: 1.5rem;
        right: 1.5rem;
    }

    &__content {
        position: relative;
        width: 100%;
        max-width: 40rem;
        min-height: 40rem;
        padding: 7rem 3rem 3rem;
        background-color: #FFFFFF;
    }

    &__content-wrapper {
        width: 100%;
        
    }

    &__title {
        padding-top: 100px;
        text-align: center;
        font-size: 5.3rem;
    }
}
