* {
    box-sizing: border-box;
}

html {
    font-size: $html-fz;
    font-family: $ff;
}

body {
    position: relative;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    font-size: 1.6rem;
}

h1, h2, h3, h4, h5, h6, ul, p {
    margin: 0;
    padding: 0;
}

ul {
    list-style-type: none;
}

.page-container {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    min-height: 100vh;
    max-width: 120rem;
    padding: 0 1.5rem;
    margin: 0 auto;
    background-color: $c-grey;
}

.btn {
    display: inline-block;
    padding: 0.8rem 1.5rem;
    font-size: 2rem;
    color: #FFFFFF;
    border-radius: 0.5rem;
    background-color: $c-blue-l;
    border: none;
    text-decoration: none;
    cursor: pointer;
    transition: background-color 0.3s;
    &:hover {
        background-color: lighten($c-blue-l,3%);
    }
}

.top-line {
    position: fixed;
    top: 0;
    left: 0;
    width: 1px;
    height: 1px;
    opacity: 0;
    visibility: hidden;
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,.7);
    opacity: 0;
    visibility: hidden;
    overflow-y: auto;
    overflow-x: hidden;
    overflow: -moz-scrollbars-none;
    -ms-overflow-style: none;
    
    &::-webkit-scrollbar {
        width: 0;
    }

    &:target {
        opacity: 1;
        visibility: visible;
    }
}

.mb-15 {
    margin-bottom: 1.5rem;
}

.mb-100 {
    margin-bottom: 10rem;
}