.box-container {
    display: grid;
    grid-template-columns: 33.333% 50.427% 1fr;
    grid-auto-rows: 19.8rem;
    grid-gap: 1.5rem;
    background-color: $c-green-l;
    counter-reset: nomer;

    @media(max-width:768px) {
        grid-gap: 1rem;
    }
}

.box {
    position: relative;
    min-height: 57px;
    color: #FFFFFF;
    font-size: 5rem;
    background-color: $c-green;

    @media(max-width:768px) {
        font-size: 3rem;
    }

    &--long-bottom-left {
        grid-column         : 1/span 2;
        grid-row            : 2;
    }

    &::before {
        counter-increment:nomer 1;
        content:counter(nomer);
        position: absolute;
        top: 50%;
        left: 50% ;
        transform: translate(-50%,-50%);
    }
}

.cube-task {
    display: grid;
    grid-template-columns: repeat(4,1fr);
    grid-gap: 3rem;
    padding: 3rem;
    background-color: $c-grey-d;

    @media(max-width:768px) {
        grid-gap: 1.5rem;
        padding: 1.5rem;
    }

    &__item {
        padding-bottom: 78%;
        background-color: $c-teal;
    }
}

